import { apiResquest } from '@/utils/request'

//
export function GetBusiness(parameter) {
    return apiResquest({
        url: '/Org/Business/Get',
        method: 'post',
        data: parameter
    });
}
//
export function GetBusinessConfig(parameter) {
    return apiResquest({
        url: '/Org/Business/Config/Get',
        method: 'post',
        data: parameter
    });
}