import { apiResquest } from '@/utils/request'

//
export function GetStoreList(parameter) {
    return apiResquest({
        url: '/Org/Store/GetList',
        method: 'post',
        data: parameter
    });
}
//
export function GetStore(parameter) {
    return apiResquest({
        url: '/Org/Store/Get',
        method: 'post',
        data: parameter
    });
}
//
export function GetAreaList(parameter) {
    return apiResquest({
        url: '/Org/Store/Area/GetList',
        method: 'post',
        data: parameter
    });
}